@import '../../../theme/index';

.wallet {
  padding: 0.25rem 1rem 0.25rem 0.5rem;
}

.hashImage {
  display: flex;
  align-items: center;
  justify-items: center;
  margin-right: 0.5rem;
  background-color: $white;
  border: 2px solid $white;
  border-radius: $border-radius-pill;
}

.chain {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.chainName {
  margin-left: 0.5rem;
}

.icon {
  height: 1.5rem;
}
