@import '../../../theme/index';

.host {
  padding: 0;
}

.brand {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: $white;
  margin-right: 3rem;
  cursor: pointer;
  padding: 0.5rem 1rem;
}

.logo {
  height: 2rem;
  margin-right: 1rem;
}

.wordmark {
  font-size: 1.75rem;
  font-family: $font-family-heading;
  color: $white;
  text-decoration: none;

  &:hover,
  &:active,
  &:visited {
    color: $white;
  }
}

.navToggle {
  margin-right: 1rem;
}

.nav {
  align-self: stretch;
  align-items: stretch;
  margin-right: 1rem;
}

.connect {
  justify-self: flex-end;
  align-items: stretch;
  padding: 0.5rem 1rem;
}

@include media-breakpoint-down(lg) {
  .connect {
    position: absolute;
    top: calc(100% + 0.5rem);
    right: 1rem;
  }
}
