@import '../../../theme/index';

.host {
  margin-top: 6rem;
}

.heroImage {
  max-width: 50vw;
  padding: 1rem 3rem 3rem 1rem;
  filter: drop-shadow(2px 2px 0px $white) drop-shadow(6px 6px 0px $red-300)
    drop-shadow(12px 12px 0px $red-300)
    drop-shadow(12px 24px 24px rgba($black, 0.25));
  transform: skew(-5deg, -5deg);
}

.title {
  font-family: $font-family-heading;
  font-weight: 600;
}

.callToAction {
  font-family: $font-family-heading;
  font-weight: 600;
  color: #ff0000;
  margin-bottom: 2rem;
}

@include media-breakpoint-down(md) {
  .heroImage {
    display: block;
    margin: auto;
  }
  .host {
    margin-top: 2rem;
  }
}
