// Zephyr 5.2.0
// Bootswatch

// Navbar

.navbar {
  font-size: $font-size-base;
  font-weight: 500;

  .nav-item {
    margin-right: 0.5rem;
    margin-left: 0.5rem;
  }

  .navbar-nav {
    width: 100%;

    .nav-link {
      display: flex;
      align-items: flex-end;
      height: 100%;
      padding: 0.375rem 1.25rem;
      color: $gray-500;
      background-color: transparent;
      border-radius: 0;
      border: 8px solid transparent;
    }
  }
}

.navbar-dark {
  .navbar-nav {
    .nav-link {
      &:hover {
        color: $gray-300;
        border-bottom-color: darken($primary, 25%);
      }

      &.active {
        color: $white;
        border-bottom-color: $primary;
      }
    }
  }
}

.navbar-light {
  .navbar-nav {
    .nav-link {
      &:hover {
        color: $gray-700;
        border-bottom-color: lighten($primary, 25%);
      }

      &.active {
        color: $black;
        border-bottom-color: $primary;
      }
    }
  }
}

// Buttons

.btn {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.btn-light,
.btn-outline-light {
  color: $gray-900;

  &:disabled,
  &.disabled {
    border: 1px solid shade-color($secondary, 10%);
  }
}

.btn-light,
.btn-outline-light {
  border-color: shade-color($light, 10%);

  &:hover,
  &:active {
    background-color: shade-color($light, 10%);
    border-color: shade-color($light, 10%);
  }
}

// Tables

.table {
  font-size: $font-size-sm;
  box-shadow: $box-shadow-lg;
}

thead th {
  font-size: $font-size-sm;
  text-transform: uppercase;
}

// Forms

.input-group-text {
  box-shadow: $box-shadow;
}

// Navs

.nav-tabs {
  font-weight: 500;

  .nav-item {
    margin: 0 0.5rem;
  }

  .nav-link {
    padding: 1rem 1.5rem;
    border-width: 1px 1px 4px 1px;
    border-color: transparent;
    border-radius: $border-radius $border-radius 0 0;
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    border-bottom-color: $primary;
  }
}

.nav-pills {
  font-weight: 500;
}

.pagination {
  font-size: $font-size-sm;
  font-weight: 500;

  .page-link {
    box-shadow: $box-shadow;
  }
}

.breadcrumb {
  font-size: $font-size-sm;
  font-weight: 500;
  border: 1px solid $gray-300;
  border-radius: $border-radius;
  box-shadow: $box-shadow;

  &-item {
    padding: 1rem 0.5rem 1rem 0;
  }
}

.breadcrumb-item + .breadcrumb-item::before {
  padding-right: 1rem;
}

// Indicators

.alert {
  .btn-close {
    color: inherit;
  }
}

.badge {
  &.bg-secondary,
  &.bg-light {
    color: $gray-900;
  }
}

// Containers

.list-group-item,
.card {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  .h1,
  .h2,
  .h3,
  .h4,
  .h5,
  .h6 {
    color: inherit;
  }

  .card-title {
    margin-bottom: 0;
  }
}

.list-group {
  box-shadow: $box-shadow-lg;
}

.card {
  box-shadow: $box-shadow-lg;
}

.modal-footer {
  background-color: $gray-100;
}

.modal-content {
  box-shadow: $box-shadow-lg;
}
